.sales {
	margin-top: 65px;

	&__list {
		@include flex-space();
		flex-wrap: wrap; }

	&__item {
		@include size(360,260);
		margin-bottom: 30px;
		box-sizing: border-box;
		position: relative;
		&_woman {
			background-image: url("../img/sales-woman.jpg"); }
		&_man {
			background-image: url("../img/sales-man.jpg"); }
		&_kids {
			background-image: url("../img/sales-kids.jpg"); }
		&_accesories {
			background-image: url("../img/sales-accesories.jpg"); }
		&_lower {
			width: 100%;
			height: 180px; } }

	&__link {
		@include size(full,full);
		margin: 0px;
		@include flex-center(column);
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
		text-decoration: none;

		&::before,
		&::after {
			content: '';
			@include size(0,0);
			box-sizing: border-box;
			display: block;
			border: none;
			transition-duration: 1s;
			opacity: 0; }

		&::before {
			@include offset(0,false,false,0);
			border-top: solid 5px $pink;
			border-left: solid 5px $pink; }

		&::after {
			@include offset(false,0,0,false);
			border-right: solid 5px $pink;
			border-bottom: solid 5px $pink; }

		&:hover {
			&::before,
			&::after {
				opacity: 1; } }

		&:hover {
			&::before,
			&::after {
				@include size(full,full);
				transition-duration: 1s; } }

		&:active {
			&::before,
			&::after {
				background-color: white;
				opacity: 0; } } }

	&__text {
		@include font(16,19);
		text-transform: uppercase;
		color: white; }

	&__item-title {
		@include font(24,29,700);
		color: $pink;
		text-transform: uppercase; } }
